/* Nav */

.nav {
  padding: 20px 0;
  /* background-color: var(--nav-bg);
  border-bottom: 1px solid var(--nav-border); */
  color: var(--nav-text);
  letter-spacing: normal;
  margin-top: 32px;
  background: var(--nav-bg);
  backdrop-filter: blur(2px);
}

.nav-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 30px;
  row-gap: 20px;
  flex-wrap: wrap;
}

.logo {
  margin-right: auto;
  color: var(--nav-text);
  font-size: 24px;
}

.logo strong {
  font-weight: 700;
}

.nav-list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 10px;
  align-items: center;
  column-gap: 40px;
  font-size: 16px;
  font-weight: 500;
}

.nav-list__link {
  color: var(--nav-text);
  transition: opacity 0.2s ease-in;
}

.nav-list__link:hover {
  opacity: 0.8;
}

.nav-list__link--active {
  position: relative;
}

.nav-list__link--active::before {
  content: "";

  position: absolute;
  left: 0;
  top: 100%;

  display: block;
  height: 2px;
  width: 100%;
  background-color: var(--nav-text);
}

.social {
  display: flex;
  column-gap: 30px;
  align-items: center;
}
.social__item a svg {
  fill: var(--nav-icon);
}
